import {
	useState,
	useEffect,
	useRef,
	useImperativeHandle,
	forwardRef,
	useCallback,
} from "react";
import Form from "./../FormData/Form";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
	faTrashCan,
	faSearch,
	faUserEdit,
	faQuestion,
	faListNumeric,
} from "@fortawesome/free-solid-svg-icons";
import { FixedSizeGrid as Grid } from "react-window";
import AutoSizer from "react-virtualized-auto-sizer";
import { Db } from "../../../database/Db_new";
import {
	splitArray,
	GetDatasAt,
	UpdateDataAt,
	DeleteAt,
} from "../../../helper";
import styles from "./../main.module.scss";
import { toast } from "react-toastify";
import ViewDetail from "./../ViewDetail";
import QTest from "./Test";
const GUTTER_SIZE = 16;
const toastBottomPos = {
	POSITION: toast.POSITION.BOTTOM_CENTER,
};
const SCREENS = {
	View: Symbol("view"),
	Form: Symbol("form"),
	Quiz: Symbol("quiz"),
};
const ListTypes = {
	CardItem: Symbol("card"),
	ListItem: Symbol("item"),
};
// hàm lọc giá trị
async function filterData(key, str, flag = "global") {
	let fn = (item) => {
		let parent = item[key].split(" ");
		let strArray = str.split(" ");
		if (key === "hanzi")
			return [...item.hanzi].some((v) => [...str].includes(v));
		return parent.some((v) => {
			return strArray.includes(v.toLowerCase());
		});
	};
	if (flag === "global")
		return Db.transaction("rw", Db.datas, async function () {
			let data = await Db.datas.orderBy("_id").filter(fn).toArray();
			return Promise.resolve(data);
		});
	return Promise.resolve(
		JSON.parse(sessionStorage.getItem("cache-data")).filter(fn) || []
	);
}

const Item = (data, style, props) => {
	return (
		<>
			{data && (
				<div
					key={data._id}
					style={{
						...style,
						left: style.left + GUTTER_SIZE,
						top: style.top + GUTTER_SIZE,
						width: style.width - GUTTER_SIZE,
						height: style.height - GUTTER_SIZE,
					}}
					className={styles["card"]}
				>
					<div
						className={styles["content"]}
						onClick={() => {
							props.viewDetailRef.current?.setViewDetail(data);
						}}
					>
						<div className={styles["front"]}>{data.hanzi}</div>
						<div className={styles["back"]}>
							<div>
								<div>{data.pinyin}</div>
								<div>{data.han_viet}</div>
								<div>{data.Vietnamese_meaning}</div>
							</div>
						</div>
					</div>
				</div>
			)}
		</>
	);
};
function ListView({ tagActive, props, datas }) {
	return (
		<ul className={styles["ul"]}>
			{datas?.map((item) => {
				return (
					<li
						key={item._id}
						onClick={() => {
							props.viewDetailRef.current?.setViewDetail(item);
						}}
					>
						<span
							onClick={(e) => {
								e.stopPropagation();
								const utterThis = new SpeechSynthesisUtterance(
									item.hanzi
								);

								if (!speechSynthesis.speaking) {
									utterThis.lang = "zh-CN";
									utterThis.volume = 1;
									speechSynthesis.speak(utterThis);
								}
							}}
						>
							{item.hanzi}
						</span>
						<span>{item.pinyin}</span>
						<span>{item.han_viet}</span>
						<span>{item.Vietnamese_meaning}</span>
						<button
							onClick={(e) => {
								e.stopPropagation();
								UpdateDataAt("tags", "_id", {
									key: tagActive._id,
									field: "QTestArrayID",
									newValue: tagActive.QTestArrayID.filter(
										(id) => id !== item._id
									),
								}).then(() => {
									DeleteAt("datas", "_id", item._id).then(
										() => {
											toast.info(
												`deleted : ${item._id}`,
												toastBottomPos
											);
											GetDatasAt("datas", "tag_id", {
												key: tagActive._id,
											}).then(props.fn.setDatas);
										}
									);
								});
							}}
						>
							<FontAwesomeIcon
								icon={faTrashCan}
								color={"var(--BrightRed"}
							/>
						</button>
					</li>
				);
			})}
		</ul>
	);
}
function TagScreen(props, ref) {
	const listRef = useRef();
	const viewDetailRef = useRef();

	const [tag, setTag] = useState(""); // hold tag item clicked on sidebar

	const [screen, setscreen] = useState(SCREENS.View); // screen sau khi clicked tag item
	const [list, setList] = useState(ListTypes.ListItem); // mac dinh hien thi danh sach
	let [datas, setDatas] = useState([]);
	useImperativeHandle(ref, () => ({ setTag })); // truyen sang sidebar
	useEffect(() => {
		GetDatasAt("datas", "tag_id", { key: tag._id }).then(setDatas);
	}, [tag, screen]);
	useEffect(() => {
		sessionStorage.setItem("cache-data", JSON.stringify(datas));
		return () => {
			sessionStorage.setItem("cache-data", JSON.stringify([]));
		};
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [tag, datas]);
	const hanldSubmit = useCallback((e) => {
		e.preventDefault();
		const data = Array.from(new FormData(e.target).entries()).reduce(
			(memo, [key, v]) => ({
				...memo,
				[key]: v,
			}),
			{}
		);
		console.log("search data : ", data);
		let s_value = data["search-value"].trim().toLowerCase();
		if (s_value) {
			if (data["search-wrapper"]) {
				// default , search in global
				filterData(data["search-type"], s_value).then(setDatas);
			} else {
				// search in local
				filterData(data["search-type"], s_value, "local").then(
					setDatas
				);
			}
		}
	}, []);
	return (
		<div className={styles["wrapper"]}>
			{screen === SCREENS.View && (
				<>
					<ViewDetail ref={viewDetailRef} />
					<div ref={listRef} className={styles["list-wrapper"]}>
						{!(list === ListTypes.ListItem) ? (
							<AutoSizer>
								{({ height, width }) => {
									let numcol = 5;
									if (width < 920) {
										numcol = 4;
									}
									if (width < 645) {
										numcol = 3;
									}
									if (width < 375) {
										numcol = 2;
									}
									// convert 1d array to numcol d
									let reshapeData = splitArray(datas, numcol);
									return (
										<Grid
											className='List'
											columnWidth={
												width / numcol -
												GUTTER_SIZE / numcol
											}
											rowHeight={height / 2}
											innerElementType={forwardRef(
												({ style, ...rest }, ref) => (
													<div
														ref={ref}
														style={{
															...style,
															paddingLeft:
																GUTTER_SIZE,
															paddingTop:
																GUTTER_SIZE,
														}}
														{...rest}
													/>
												)
											)}
											rowCount={reshapeData.length}
											columnCount={numcol}
											height={height}
											width={width}
										>
											{({
												columnIndex,
												rowIndex,
												style,
											}) => {
												let data =
													reshapeData[rowIndex][
														columnIndex
													];
												return Item(data, style, {
													viewDetailRef,
													tag,
													setDatas,
												});
											}}
										</Grid>
									);
								}}
							</AutoSizer>
						) : (
							<ListView
								datas={datas}
								tagActive={tag}
								props={{
									viewDetailRef,
									fn: setDatas,
								}}
							/>
						)}
					</div>
					<div className={styles["btns"]}>
						<div className={styles["wrapper"]}>
							{
								<>
									<label className={styles["slide-input"]}>
										<input
											onChange={(e) => {
												listRef?.current.style.setProperty(
													"--Hanzi_FontSize",
													`${e.target.value}rem`
												);
											}}
											type='range'
											defaultValue={2.5}
											step={0.5}
											min={1}
											max={5}
										/>
									</label>
									<div className={styles["search-wrapper"]}>
										<form onSubmit={hanldSubmit}>
											<div>
												<select name='search-type'>
													<option
														value='hanzi'
														defaultValue='hanzi'
													>
														hanzi
													</option>
													<option value='pinyin'>
														pinyin
													</option>
													<option value='Vietnamese_meaning'>
														Vietnamese
													</option>
												</select>
												<label>
													<input
														type='checkbox'
														defaultChecked={true}
														name='search-wrapper'
													/>{" "}
													-g
												</label>
											</div>

											<div
												className={
													styles["input-wrapper"]
												}
											>
												<input
													type='search'
													name='search-value'
													placeholder='search ...'
												/>
												<button type='submit'>
													<FontAwesomeIcon
														icon={faSearch}
													/>
												</button>
											</div>
										</form>
										<button
											onClick={() => {
												GetDatasAt("datas", "tag_id", {
													key: tag._id,
												}).then(setDatas);
											}}
										>
											re-load
										</button>
									</div>
									<button
										title={"form"}
										onClick={() => setscreen(SCREENS.Form)}
									>
										<FontAwesomeIcon icon={faUserEdit} />
									</button>
									<button
										onClick={() => setscreen(SCREENS.Quiz)}
									>
										<FontAwesomeIcon icon={faQuestion} />
									</button>
									<button
										onClick={() => {
											setList((prev) => {
												return prev ===
													ListTypes.ListItem
													? ListTypes.CardItem
													: ListTypes.ListItem;
											});
										}}
									>
										<FontAwesomeIcon
											color={`${
												!(list === "list")
													? ""
													: "var(--LimeGreen)"
											}`}
											icon={faListNumeric}
										/>
									</button>
								</>
							}
						</div>
					</div>
				</>
			)}
			{screen === SCREENS.Form && (
				<div className={styles["form-wrapper"]}>
					<Form
						tag={tag}
						back={() => {
							setscreen(SCREENS.View);
						}}
					/>
				</div>
			)}
			{screen === SCREENS.Quiz && (
				<QTest
					tag={tag}
					datas={datas}
					props={{
						back: () => {
							setscreen(SCREENS.View);
						},
					}}
				/>
			)}
		</div>
	);
}

export default forwardRef(TagScreen);
